import React, { Component } from 'react'
import '@/assets/scss/booking.scss'
import { apiGetBooking } from '@/modules/api'
class Booking extends Component {
  state = {
    currentYear: new Date().getFullYear(),
    currentMonth: new Date().getMonth() + 1,
    currentDate: new Date().getDate(),
    days: new Date(new Date().getFullYear(), new Date().getMonth() + 1, 0).getDate(),
    AllBookList: {},
    bookList: {},
    filterTeacher: '',
    filterCourse: 1,
    filterTime: ''
  }

  componentDidMount () {
    this.bookListHandler(this.state.currentMonth)
  }

  componentDidUpdate (prevProps, prevState) {
    if (prevState.currentMonth !== this.state.currentMonth) {
      console.log('call')
      this.bookListHandler(this.state.currentMonth)
    }
  }

  bookListHandler = (month) => {
    apiGetBooking({
      month: month
    }).then(res => {
      const { data, status } = res.data
      const bookObj = {}
      for (const key in data) {
        bookObj[`${data[key].booking_date - 1}T${data[key].booking_time}`] = data[key]
      }
      if (status === 200) {
        this.setState({
          bookList: bookObj,
          AllBookList: bookObj
        })
      }
    }).catch(err => {
      console.log(err)
    })
  }

  monthHandler = (num) => {
    const { currentMonth, currentYear } = this.state
    if (currentMonth >= 12 && num > 0) {
      this.setState({ currentMonth: 1 })
      this.setState(() => {
        return {
          currentYear: currentYear + 1
        }
      })
      this.setState({ days: new Date(currentYear + 1, 1, 0).getDate() })
      return
    }
    if (currentMonth <= 1 && num < 0) {
      this.setState({ currentMonth: 12 })
      this.setState(() => {
        return {
          currentYear: currentYear - 1
        }
      })
      this.setState({ days: new Date(currentYear + 1, 12, 0).getDate() })
      return
    }
    this.setState(() => {
      return { currentMonth: currentMonth + num }
    })
    this.setState({ days: new Date(currentYear + 1, currentMonth + num, 0).getDate() })
  }

  firstWeekdayHandler = (currentYear, currentMonth) => {
    return new Date(currentYear + '/' + currentMonth + '/1').getDay()
  }

  teacherHandler = (teacher) => {
    const { AllBookList, filterTime } = this.state
    const newBookList = {}
    if (teacher !== '') {
      if (filterTime !== '') {
        for (const key in AllBookList) {
          if (Number(AllBookList[key].booking_teacher_id) === Number(teacher) && Number(AllBookList[key].booking_time) === Number(filterTime)) {
            newBookList[key] = AllBookList[key]
          }
        }
      } else {
        for (const key in AllBookList) {
          if (Number(AllBookList[key].booking_teacher_id) === Number(teacher)) {
            newBookList[key] = AllBookList[key]
          }
        }
      }
      this.setState({ bookList: newBookList, filterTeacher: teacher })
      return
    }

    if (filterTime !== '') {
      for (const key in AllBookList) {
        if (Number(AllBookList[key].booking_time) === Number(filterTime)) {
          newBookList[key] = AllBookList[key]
        }
      }
    }
    this.setState({ bookList: AllBookList, filterTeacher: teacher })
  }

  timeHandler = (time) => {
    console.log(time)
    const { AllBookList, filterTeacher } = this.state
    const newBookList = {}
    if (time !== '') {
      if (filterTeacher !== '') {
        for (const key in AllBookList) {
          if (Number(AllBookList[key].booking_time) === Number(time) && Number(AllBookList[key].teacher_sid) === Number(filterTeacher)) {
            newBookList[key] = AllBookList[key]
          }
        }
      } else {
        for (const key in AllBookList) {
          if (Number(AllBookList[key].booking_time) === Number(time)) {
            newBookList[key] = AllBookList[key]
          }
        }
      }
      this.setState({ bookList: newBookList, filterTime: time })
      return
    }

    if (filterTeacher !== '') {
      for (const key in AllBookList) {
        if (Number(AllBookList[key].teacher_sid) === Number(filterTeacher)) {
          newBookList[key] = AllBookList[key]
        }
      }
    }
    this.setState({ bookList: AllBookList, filterTime: time })
  }

  render () {
    const { currentYear, currentMonth, days, bookList } = this.state
    const firstWeekday = this.firstWeekdayHandler(currentYear, currentMonth)
    const firstWeekdayArr = []
    const daysArr = []
    firstWeekdayArr.length = firstWeekday
    firstWeekdayArr.fill(1)
    daysArr.length = days
    daysArr.fill(1)
    const firstNode = firstWeekdayArr.map((_, i) => {
      return (
        <li className="calendar-days-content disabled" key={i} >
        </li >
      )
    })
    const midNode = daysArr.map((_, i) => {
      return (
        (bookList[`${i}T0`] || bookList[`${i}T1`])
          ? (<li className="calendar-days-content" key={i}>
            <p>
              {currentMonth}/{i + 1}號
            </p>
            {
            bookList[`${i}T0`] && (
            <div className="calendar-days-content-afternoon">
              <h5>
                <span>
                  <i className="fas fa-sun"></i> :
                  {
                   bookList[`${i}T0`].course_name
                  }
                </span>
                <span>
                  人數
                  {
                   bookList[`${i}T0`].booking_total
                  }
                </span>
              </h5>
              <div>
                老師 : {
                 bookList[`${i}T0`].teacher_name
                }
              </div>
              <div className="options">
                <i className="fas fa-info-circle"></i><i className="fas fa-shopping-cart"></i><i className="fas fa-money-bill-wave"></i>
              </div>
            </div>)
            }

            {
            bookList[`${i}T1`] && (
            <div className="calendar-days-content-night">
              <h5>
                <span>
                  <i className="fas fa-moon"></i> :
                  {
                    bookList[`${i}T1`].course_name
                  }
                </span>
                <span>
                  人數
                  {
                    bookList[`${i}T1`].booking_total
                  }
                </span>
              </h5>
              <div>
                老師 : {
                  bookList[`${i}T1`].teacher_name
                }
              </div>
              <div className="options">
                <i className="fas fa-info-circle"></i><i className="fas fa-shopping-cart"></i><i className="fas fa-money-bill-wave"></i>
              </div>
            </div>)
            }
          </li>)
          : (<li className="calendar-days-content disabled" key={i} ></li >)
      )
    })

    return (
      <div className="booking">
        <div className="search-options">
          <label htmlFor="">
            找老師:
            <select name="" id="" onChange={(e) => this.teacherHandler(e.target.value)}>
              <option value="">All</option>
              <option value="1">1</option>
              <option value="2">2</option>
              <option value="3">3</option>
              <option value="4">4</option>
              <option value="5">5</option>
            </select>
          </label>
          <label htmlFor="">
            找課程:
            <select name="" id="">
              <option value="">All</option>
            </select>
          </label>
          <label htmlFor="">
            時段:
            <select name="" id="" onChange={(e) => this.timeHandler(e.target.value)}>
              <option value="">All</option>
              <option value="0">下午</option>
              <option value="1">晚上</option>
            </select>
          </label>
        </div>
        <div className="calendar">
          <div className="calendar-header">
            <div>
              <i className="fas fa-chevron-left" onClick={() => this.monthHandler(-1)}></i>
              <p>{currentYear}年</p>
              <p>{currentMonth}月</p>
              <i className="fas fa-chevron-right" onClick={() => this.monthHandler(1)}></i>
            </div>
            <div>
              <i className="fas fa-sun"></i>14:00 ~ 18:00
              <i className="fas fa-moon"></i>19:00 ~ 21:00
            </div>
          </div>
          <div className="calendar-body">
            <ul className="calendar-week">
              <li>星期日</li>
              <li>星期一</li>
              <li>星期二</li>
              <li>星期三</li>
              <li>星期四</li>
              <li>星期五</li>
              <li>星期六</li>
            </ul>
            <ul className="calendar-days">
              {firstNode}
              {midNode}
            </ul>
          </div>
        </div>
      </div >
    )
  }
}

export default Booking
